.card {
  box-sizing: border-box;
  width: 200px;
  border: 1px solid $border-color;
  border-radius: 5px;
  background: $background-color;
  padding: 32px 16px 16px;

  transition: transform 300ms;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    color: $main-text-color;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;

    margin-bottom: 4px;
  }

  &:hover &__title {
    color: $hover-title-color;
  }

  &__product-code {
    color: $secondary-text-color;

    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    margin-bottom: 24px;

    &-label {
      color: $main-text-color;
      text-align: right;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    line-height: 18px;

    margin-bottom: 16px;

    &-label {
      color: $secondary-text-color;
      line-height: 18px;
    }

    &-value {
      color: $main-text-color;
      text-align: right;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 166px;
    height: 40px;
    color: $background-color;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;

    text-decoration: none;
    border: 1px solid $button-color;
    border-radius: 5px;
    background: $button-color;

    &:hover {
      background: $background-color;
      color: $button-color;

    }
  }
}
